import axios from "axios";
import jwtAxios from "../util/jwtUtil";
import { API_SERVER_HOST } from "./memberApi";
//const prefix = `${API_SERVER_HOST}/api/chat`;
const prefix = `/api/chat`;

export const chatGetPost = async ({ chatroomname, chatroompassword }) => {
  //console.log(
  //  "api/chat.js chatGetPost ë§¤ê°œë³€ìˆ˜í™•ì¸ chatroomname -> ",
  //  chatroomname
  //);
  //console.log(
  //  "api/chat.js chatGetPost ë§¤ê°œë³€ìˆ˜í™•ì¸ chatroompassword -> ",
  //  chatroompassword
  //);
  const res = await jwtAxios.post(`${prefix}/${chatroomname}`, {
    chatroompassword,
  });

  return res.data;
};

export const chatList = async () => {
  const res = await jwtAxios.get(`${prefix}/list`);
  return res.data;
};

